*,
*::before,
*::after {
  box-sizing: border-box;
}

* {
  margin: 0;
}

html,
body {
  height: 100%;
}

body {
  line-height: 1.5;
  -webkit-font-smoothing: antialiased;
}

img,
picture,
video,
canvas,
svg {
  display: block;
}

input,
button,
textarea,
select {
  font: inherit;
}

p,
h1,
h2,
h3,
h4,
h5,
h6 {
  overflow-wrap: break-word;
}

#root,
#__next {
  isolation: isolate;
}

ul[role=list],
ol[role=list] {
  list-style: none;
}

textarea {
  resize: none;
}

.mobile-nav-bar__close:hover {
  cursor: pointer;
}

@media (min-width: 375px) {
  .mobile-nav-bar {
    width: 375px;
    margin: 0 auto;
    background-color: white;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding: 1rem 1rem 1rem 1rem;
  }
  .mobile-nav-bar__menu {
    align-self: center;
  }
  .mobile-nav-bar__menu:hover {
    cursor: pointer;
  }
  .mobile-nav-bar__close {
    font-size: 20px;
  }
}
@media (min-width: 700px) {
  .nav-bar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    min-width: 700px;
    padding: 2rem 2rem 2rem 2rem;
  }
  .nav-bar__links {
    display: flex;
    list-style: none;
    gap: 21px;
  }
  .nav-bar__links:hover {
    cursor: pointer;
  }
  .nav-bar__link:visited {
    text-decoration: none;
    color: black;
  }
}
